.App {
  margin: 0px;
  padding: 0px;
  text-align: center;
  font-family: 'Blinker', sans-serif;
  font-family: 'Outfit', sans-serif;
  max-width: 100%;
}


.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.home {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-left: 3%;
}



@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

@media (max-width: 500px) {
  .App {
    color: white;
    background-repeat: no-repeat;
    background-size: cover;
  }

  .partly_cloudy {
    background-image: url('../public/assets/partly_cloudy.png');

  }

  .partly_cloudy input::placeholder,  .partly_cloudy,.partly_cloudy .navBar input, .partly_cloudy .legal{
    color: rgb(36, 36, 36) !important;
  }
  
  .sunny {
    background-image: url('../public/assets/white-cloud-blue-sky_74190-2381.jpg');
    /* Outros estilos para o clima ensolarado */
  }

  .cloudy {
    background-image: url('../public/assets/cloudy.png');
    /* Outros estilos para o clima nublado */
  }
  .light_rain {
    background-image: url('../public/assets/rainy_day.png');
    /* Outros estilos para o clima nublado */
  }
  .rain_sun {
    background-image: url('../public/assets/rainy_day.png');
    /* Outros estilos para o clima nublado */
  }

  .heavy_rain{
    background-image: url('../public/assets/heavy_rain.png');
  }
  
  
}

@media(max-height: 700px ) and (min-width: 1000px){
  
  .home{
    padding-bottom: 130px;
  }
}